import React from 'react';
import { MDBRow, MDBCol } from "mdbreact";
import SideNav from '../SideNav';

class Around extends React.Component {
    render() {
        return (
            <React.Fragment>
                <MDBRow>
                    <MDBCol md="12" className="wrap-banner-img">
                        <img src={'/dining/template/images/banner/dining_banner.jpg'} className="img-fluid" alt="" />
                    </MDBCol>
                </MDBRow>

                <MDBRow>
                    <div className="col wrap-left-col">
                        <SideNav></SideNav>
                    </div>
                    <MDBCol className="wrap-main-content">

                        <MDBRow>

                            <MDBCol md="4" className="mb-5 wrap-dining-list">
                                <h2>
                                    <div className="dining-list-header">
                                        <span className="dining-list-header-text">Vending Machines</span>
                                        <img src='/dining/template/images/icons/vendingmachine.png' alt="dining list icon" />
                                    </div>
                                </h2>
                                <p className="dining-list-description">
                                    There are over 350 vending machines on campus, each loaded with your favorite snacks and drinks.
                                </p>
                            </MDBCol>

                            <MDBCol md="4" className="mb-5 wrap-dining-list">
                                <h2>
                                    <div className="dining-list-header">
                                        <span className="dining-list-header-text">Hot Dog Food Stops</span>
                                        <img src='/dining/template/images/icons/hotdog.png' alt="dining list icon" />
                                    </div>
                                </h2>
                                <p className="dining-list-description">
                                    Harvill building.
                                </p>
                            </MDBCol>

                            <MDBCol md="4" className="mb-5 wrap-dining-list">
                                <h2>
                                    <div className="dining-list-header">
                                        <span className="dining-list-header-text">Cappuccino Cart<br/>Food Stops</span>
                                        <img src='/dining/template/images/icons/cappuccinocart.png' alt="dining list icon" />
                                    </div>
                                </h2>
                                <p className="dining-list-description">
                                    Social Sciences building.
                                </p>
                            </MDBCol>

                        </MDBRow>

                    </MDBCol>

                </MDBRow>

            </React.Fragment>
        )
    }
}

export default Around;