import React, { Component } from 'react';
import { MDBRow, MDBCol } from "mdbreact";

class MainContent extends Component {
    constructor(props) {
        super(props);
        this.state = {

        }
    }

    updateBtnSize (value, count) {
        if (value !== null) {
            count++;
        }
        return count;
    }

    render() {

        const detail = this.props.detail;
        const loading = this.props.loading;
        // let btn_count = 0;

        // btn_count = this.updateBtnSize(detail.menu, btn_count);
        // btn_count = this.updateBtnSize(detail.catering_menu, btn_count);
        // btn_count = this.updateBtnSize(detail.catering_online_order, btn_count);

        // const btn_size = (12/btn_count).toString();

        return(
            <React.Fragment>
            {
                loading ?
                null
                :
                <React.Fragment>

                    <div className="col-12">
                        <h1 className="dining-header" dangerouslySetInnerHTML={{__html:detail.name}}></h1>
                        <div className="dining-description" dangerouslySetInnerHTML={{__html:detail.description}}></div>
                    </div>

                    {/* <div className="col-12">
                        <MDBRow>
                            {
                                (detail.menu !== null) ?
                                <MDBCol md={btn_size} className="wrap-dining-btn">
                                    <a href={detail.menu} target="_blank" rel="noopener noreferrer"><img src="/dining/template/images/dining_menu_btn.png" alt="dining-btn" /></a>
                                </MDBCol>
                                :
                                null
                            }
                            {
                                (detail.catering_menu !== null) ?
                                <MDBCol md={btn_size} className="wrap-dining-btn">
                                    <a href={detail.catering_menu} target="_blank" rel="noopener noreferrer"><img src="/dining/template/images/dining_cateringmenu_btn.png" alt="dining-btn" /></a>
                                </MDBCol>
                                :
                                null
                            }
                            {
                                (detail.catering_online_order !== null) ?
                                <MDBCol md={btn_size} className="wrap-dining-btn">
                                    <a href={detail.catering_online_order} target="_blank" rel="noopener noreferrer"><img src="/dining/template/images/dining_order_btn.png" alt="dining-btn" /></a>
                                </MDBCol>
                                :
                                null
                            }
                        </MDBRow>
                    </div> */}

                    <div className="col-12 wrap-hour">
                        <b style={{color: '#70AD47'}}>HOURS OF OPERATION: </b> {detail.hour}
                    </div>

                    {
                        (detail.phone === '' || detail.phone === null) ?
                        null
                        :
                        <div className="col-12 wrap-phone">
                            <b style={{color: '#70AD47'}}>CALL US AT: </b> {detail.phone}
                        </div>
                    }

                    <div className="col-12 wrap-click-all-hours">
                        <a href="/infodesk/hours">Click Here to View All Union Hours</a>
                    </div>

                    <MDBRow className="mobile-dining-menu-btn mt-4">
                        {
                            (detail.menu !== null) ?
                            <MDBCol md="12" className="">
                                <a href={detail.menu} target="_blank" rel="noopener noreferrer"><img src="/dining/template/images/dining_menu_btn.png" alt="dining-btn" /></a>
                            </MDBCol>
                            :
                            null
                        }
                        {
                            (detail.catering_menu !== null) ?
                            <MDBCol md="12" className="">
                                <a href={detail.catering_menu} target="_blank" rel="noopener noreferrer"><img src="/dining/template/images/dining_cateringmenu_btn.png" alt="dining-btn" /></a>
                            </MDBCol>
                            :
                            null
                        }
                        {
                            (detail.catering_online_order !== null) ?
                            <MDBCol md="12" className="">
                                <a href={detail.catering_online_order} target="_blank" rel="noopener noreferrer"><img src="/dining/template/images/dining_order_btn.png" alt="dining-btn" /></a>
                            </MDBCol>
                            :
                            null
                        }
                    </MDBRow>

                </React.Fragment>
            }
            </React.Fragment>
        )
    }
}

export default MainContent;