import React, { Component } from 'react';
import { MDBContainer, MDBRow, MDBCol } from "mdbreact";
import SideNav from '../SideNav/';
import MainContent from './maincontent.js';
import SlideShow from './slideshow.js';
import './style.css';


class Dining extends Component {

    constructor(props) {
        super(props);
        this.state = {
            url: window.location.search,
            location: this.getQueryStringValue('q'),
            banner_img: null,
            loading: true,
            detail: {},
            slides: []
        }
    }

    getQueryStringValue = (key) => {
        const encoded_url = encodeURIComponent(key).replace(/[.+*]/g, "\\$&");
        const replaced_url = window.location.search.replace(new RegExp("^(?:.*[&\\?]" + encoded_url + "(?:\\=([^&]*))?)?.*$", "i"), "$1");
        // console.log('href : ', window.location.href.substr(window.location.href.lastIndexOf('/') + 1));
        // console.log('search : ', window.location.search);
        // console.log('replaced url : ', replaced_url);
        return window.decodeURIComponent(replaced_url, "$1");
    }

    componentDidMount() {
        // Check URL first
        let location = '';
        let restaurant = '';
        if (window.location.search === ''){
            // Check if last character is slash
            let href = window.location.href;
            let last_index = href.lastIndexOf('/');
            if (window.location.href.length === (last_index + 1)){
                // last char is slash, then remove
                href = href.substr(0, last_index);
                last_index = href.lastIndexOf('/');
            }

            restaurant = href.substr(last_index + 1);
            let trimmed_href = href.substr(0, href.lastIndexOf('/'));
            location = trimmed_href.substr(trimmed_href.lastIndexOf('/') + 1);
        }
        else {
            location = this.getQueryStringValue('q');
            restaurant = this.getQueryStringValue('p');
        }

        // console.log('location : ', location);
        // console.log('restaurant : ', restaurant);

        // const url = 'http://192.168.99.100/api/dining/getRestaurantDetail';
        const url = '/api/dining/getRestaurantDetail';
        const fetch_option = {
            method: 'POST',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                location: location,
                restaurant: restaurant
            })
        }

        fetch(url,fetch_option)
        .then(response => response.json())
        .then(data => {
            if (data.success){
                this.setState({
                    banner_img: data.detail.banner_img,
                    detail: data.detail,
                    slides: data.slides,
                    loading: false
                })
            }
        })
        .catch(error => { console.log(error) });
        
        // this.setState({loading: false});
    }

    render() {

        return(
            <MDBContainer>
                {
                    this.state.loading ?
                    <MDBCol md="12" style={{textAlign: 'center', height: '500px'}}>
                        <div className="su-loading" style={{marginTop: '200px', fontSize: '6em' }}>
                            <img src="/template/images/union_loading_icon.png" alt="student union loading icon" />
                        </div>
                    </MDBCol>
                    :
                    <React.Fragment>
                        <MDBRow>
                            <MDBCol md="12" className="wrap-banner-img">
                                <img src={'/dining/template/images/banner/' + this.state.banner_img} className="img-fluid" alt="" />
                            </MDBCol>
                        </MDBRow>

                        <MDBRow>

                            <React.Fragment>
                            <div className="col wrap-left-col">
                    
                                <div className="wrap-dining-btn">
                                {
                                    (this.state.detail.menu !== null) ?
                                    <MDBCol md="12" className="wrap-dining-btn mb-2">
                                        <a href={this.state.detail.menu} target="_blank" rel="noopener noreferrer"><img src="/dining/template/images/dining_menu_btn.png" alt="dining-btn" /></a>
                                    </MDBCol>
                                    :
                                    null
                                }
                                {
                                    (this.state.detail.pdf_menu !== null) ?
                                    <MDBCol md="12" className="wrap-dining-btn mb-2">
                                        <a href={this.state.detail.pdf_menu} target="_blank" rel="noopener noreferrer"><img src="/dining/template/images/dining_pdf_btn.png" alt="dining-btn" /></a>
                                    </MDBCol>
                                    :
                                    null
                                }
                                {
                                    (this.state.detail.catering_menu !== null) ?
                                    <MDBCol md="12" className="wrap-dining-btn mb-2">
                                        <a href={this.state.detail.catering_menu} target="_blank" rel="noopener noreferrer"><img src="/dining/template/images/dining_cateringmenu_btn.png" alt="dining-btn" /></a>
                                    </MDBCol>
                                    :
                                    null
                                }
                                {
                                    (this.state.detail.catering_online_order !== null) ?
                                    <MDBCol md="12" className="wrap-dining-btn mb-2">
                                        <a href={this.state.detail.catering_online_order} target="_blank" rel="noopener noreferrer"><img src="/dining/template/images/dining_order_btn.png" alt="dining-btn" /></a>
                                    </MDBCol>
                                    :
                                    null
                                }
                                </div>
                                <SideNav></SideNav>
                            </div>
                            </React.Fragment>

                            <MDBCol>
                                <MainContent
                                loading={this.state.loading}
                                detail={this.state.detail}
                                />
                                <SlideShow 
                                location={this.state.location}
                                slides={this.state.slides} 
                                />
                            </MDBCol>

                        </MDBRow>
                    </React.Fragment>
                }

            </MDBContainer>
        )
    }
}

export default Dining;