import React from 'react';
import { MDBContainer, MDBRow, MDBCol } from "mdbreact";
import SideNav from '../SideNav';

class Home extends React.Component {
    render() {
        return(
            <MDBContainer>

                <MDBRow>
                    <MDBCol md="12" className="wrap-banner-img">
                        <img src='/dining/template/images/banner/dining_banner.jpg' className="img-fluid" alt="" />
                    </MDBCol>
                </MDBRow>

                <MDBRow>
                    <div className="col wrap-left-col">
                        <SideNav></SideNav>
                    </div>
                    <MDBCol className="wrap-main-content">
                        <h1>Quality. Convenient. Healthy.</h1>

                        <p className="opening">
                            The University of Arizona offers one of the finest college dining services in the country. Our quality, variety, convenience, and healthy food options are unsurpassed.
                        </p>

                        <div className="reg-p" >
                            <p>
                                There are different eateries, conveniently situated throughout the campus to meet your dining needs for every meal, every day.
                            </p>

                            <p>
                                From cafeteria dining, to national franchises and brands - plus a variety of specialty restaurants and value added meal plans - the University of Arizona will provide you with the very best!
                            </p>

                            <br />

                            <p>
                                <a href="sumc" className="su-red-link">Student Union Memorial Center (SUMC) Restaurants &gt;</a><br />
                                <br />
                                <a href="psu" className="su-red-link">Park Student Union (PSU) Restaurants &gt;</a><br />
                                <br />
                                <a href="other" className="su-red-link">More Places to Eat &gt;</a>
                            </p>
                            
                        </div>
                    </MDBCol>
                </MDBRow>
            </MDBContainer>
        )
    }
}

export default Home;