import React, { Component } from 'react';
import { MDBCarousel, MDBCarouselCaption, MDBCarouselInner, MDBCarouselItem, MDBView, MDBMask, MDBCol } from "mdbreact";

class SlideShow extends Component {

    render() {

        const slides = this.props.slides;
        const length = slides.length;

        return(
            <MDBCol md="12" className="wrap-slideshow">
                {
                    (length === 0) ?
                    null :
                    <MDBCarousel activeItem={1} length={length} showControls={true} showIndicators={true} className="z-depth-1">
                        <MDBCarouselInner>

                            {
                                slides.map((row, index) => {
                                    return(
                                        <MDBCarouselItem itemId={index+1} key={index+1}>
                                            <MDBView>
                                                <img className="d-block w-100" src={row.filePath} alt="dining-slide-img" />
                                                {/* <MDBMask overlay="black-light" /> */}
                                            </MDBView>
                                            <MDBCarouselCaption>
                                                <h3 className="h3-responsive"></h3>
                                                <p></p>
                                            </MDBCarouselCaption>
                                        </MDBCarouselItem>
                                    );
                                })
                            }

                        </MDBCarouselInner>
                    </MDBCarousel>
                }
            </MDBCol>
        )
    }
}

export default SlideShow;