import React from 'react';
import { MDBContainer, MDBRow, MDBCol, MDBIcon } from "mdbreact";
import SideNav from '../SideNav';
import Around from './around.js';
import './style.css';

class List extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            banner_img: null,
            restaurants: [],
            loading: false
        }
    }

    getQueryStringValue = (key) => {
        const encoded_url = encodeURIComponent(key).replace(/[.+*]/g, "\\$&");
        const replaced_url = window.location.search.replace(new RegExp("^(?:.*[&\\?]" + encoded_url + "(?:\\=([^&]*))?)?.*$", "i"), "$1");
        return window.decodeURIComponent(replaced_url, "$1");
    }

    componentDidMount() {
        let location = '';
        if (window.location.search === ''){
            // Check if last character is slash
            let href = window.location.href;
            let last_index = href.lastIndexOf('/');
            if (window.location.href.length === (last_index + 1)){
                // last char is slash, then remove
                href = href.substr(0, last_index);
                last_index = href.lastIndexOf('/');
            }

            location = href.substr(last_index + 1);
        }
        else {
            location = this.getQueryStringValue('q');
        }
        this.setState({location: location});

        // Still location is empty, then take to dining main page


        // const url = 'http://localhost' + '/api/dining/getRestaurants';
        const url = '/api/dining/getRestaurants';
        const fetch_option = {
            method: 'POST',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                location: location
            })
        }

        fetch(url,fetch_option)
        .then(response => response.json())
        .then(data => {
            if (data.success){
                this.setState({
                    banner_img: data.banner_img,
                    restaurants: data.restaurants,
                    loading: false
                })
                // console.log(data.restaurants);
            }
        })
        .catch(error => { console.log(error) });
    }

    render() {
        return(
            <React.Fragment>
            {
                this.state.loading ?
                <MDBCol md="12" style={{textAlign: 'center', height: '500px'}}>
                    <div className="su-loading" style={{marginTop: '200px', fontSize: '6em' }}>
                        <img src="/template/images/union_loading_icon.png" alt="student union loading" />
                    </div>
                </MDBCol>
                :
                <MDBContainer>
                    {
                        this.state.location === "around" ?
                        <Around />
                        :
                        <React.Fragment>
                            <MDBRow>
                                <MDBCol md="12" className="wrap-banner-img">
                                    <img src={'/dining/template/images/banner/' + this.state.location + '_banner.jpg'} className="img-fluid" alt="" />
                                </MDBCol>
                            </MDBRow>

                            <MDBRow>
                                <div className="col wrap-left-col">
                                    <SideNav></SideNav>
                                </div>
                                <MDBCol className="wrap-main-content">
                                    <MDBRow>
                                    {
                                        this.state.restaurants.map((row, index) => {
                                            return(
                                                <MDBCol md="4" key={'restaurant' + index} className="mb-5 wrap-dining-list">
                                                    <h2>
                                                        <a href={row.location_url} className="dining-list-header">
                                                            <span className="dining-list-header-text" dangerouslySetInnerHTML={{__html:row.location_name}}></span>
                                                            <img src={'/dining/template/images/icons/' + row.short_name + '.png'} alt="dining list icon" />
                                                            {/* <span className="dining-list-header-border"><img src={'/dining/template/images/icons/' + row.short_name + '.png'} alt="dining list icon" /></span> */}
                                                            {/* <img src= "https://i.ibb.co/HTxGFtc/more.png"></img> */}
                                                        </a>
                                                    </h2>
                                                    <p className="dining-list-description">{row.short}</p>
                                                    <a href={row.location_url} className="dining-list-link"><MDBIcon icon="angle-double-right" /> Check hours and menu</a>
                                                    {/* <div className="dining-list-bottom">
                                                        <a>Menu</a>
                                                    </div> */}
                                                </MDBCol>
                                            );
                                        })
                                    }
                                    </MDBRow>
                                </MDBCol>
                            </MDBRow>
                        </React.Fragment>                        

                    }

                </MDBContainer>
            }
            </React.Fragment>
        )
    }
}

export default List;