import 'react-app-polyfill/ie9';
import 'react-app-polyfill/ie11';   
import React from 'react';
import ReactDOM from 'react-dom';
import "mdbreact/dist/css/mdb.css";
import Home from './components/Home';
import List from './components/List';
import Dining from './components/Dining/dining.js';
import * as serviceWorker from './serviceWorker';

if (document.getElementById('dining_home')){
    ReactDOM.render(<Home />, document.getElementById('dining_home'));
}

if (document.getElementById('dining_list')){
    ReactDOM.render(<List />, document.getElementById('dining_list'));
}

if (document.getElementById('dining')){
    ReactDOM.render(<Dining />, document.getElementById('dining'));
}
// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
